import {ApplicationRef, ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-subject',
  templateUrl: './subject.component.html',
  styleUrls: ['./subject.component.css']
})
export class SubjectComponent implements OnInit {
  @Input() grdP: number;
  @Output() grdPChange: EventEmitter<number> = new EventEmitter();
  @Input() credit: number;
  @Output() creditChange: EventEmitter<number> = new EventEmitter();
  wasChanged = false;

  constructor(private ref: ApplicationRef) { }

  ngOnInit() {
  }



  onChangePointer(event) {
    const change = event.target.value;
    console.log('OnChangePointer=', change);
    if (isNaN(change)) {
      this.grdP = 0;
    } else {
      this.grdP = +change;
      console.log('Changed Pointer:', this.grdP);
    }
    this.grdPChange.emit(this.grdP);
    this.ref.tick();
  }

  onChangeCredit(event) {
    const change = event.target.value;
    if (isNaN(change)) {
      this.credit = 0;
    } else {
      this.credit = +change;
      console.log('Changed Credit:', this.credit);
    }
    this.creditChange.emit(this.credit);
    this.ref.tick();
  }
}
