import {Component, ViewChild} from '@angular/core';
import {SubContainerComponent} from './sub-container/sub-container.component';
import {ToastrService} from 'ngx-toastr';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  @ViewChild(SubContainerComponent, {static: true})
    child: SubContainerComponent;
  gpaCalcOnce = false;
  title = 'gpa-calc';
  gpa = 0;

  constructor(private toastr: ToastrService) {}

  gpaDidChange(newGPA: number) {
    console.log('GPADIDCHANGE:', newGPA);
    this.gpa = newGPA;
  }

  triggerCalculate() {
    this.child.recalcGPA();
  }

  gpaCalculatedOnce() {
    this.gpaCalcOnce = true;
  }

  hasLocalStorage() {
    return(localStorage.length > 0);
  }

  clearLocalStorage() {
    localStorage.clear();
    this.gpa = 0.00;
    this.gpaCalcOnce = false;
    this.child.resetDefaults();
    this.toastr.success('Cleared all data!', 'Poof!');
  }
}
